import React, { useEffect } from 'react';
import { Button } from './Button';
import './Pricing.css';
import { FaFire } from 'react-icons/fa';
import { BsXDiamondFill } from 'react-icons/bs';
import { GiCrystalize } from 'react-icons/gi';
import { IconContext } from 'react-icons/lib';
import { Link } from 'react-router-dom';
import Aos from 'aos';
import 'aos/dist/aos.css';

function Pricing() {
  useEffect(() => {
    Aos.init({ duration:2000 });
    }, []);

  return (
    <IconContext.Provider value={{ color: '#fff', size: 64 }}>
      <div className='pricing__section'>
        <div className='pricing__wrapper'>
          <h1 className='pricing__heading'>Portfolio</h1>
          <div className='pricing__container' >
            <Link to='/portfolio' className='pricing__container-card' data-aos='flip-right'>
              <div className='pricing__container-cardInfo' >
                <div className='icon'>
                  <FaFire />
                </div>
                
                <ul className='pricing__container-features'>
                  <li> CATV mini head-end at the UPTH resident doctors’ quarters</li>
                  <li>CATV mini head-end and installed an intercom PABX.</li>
                  <li>Cabling work (SOC) for intercom equipment (MITEL 200 lite PABX) </li>
                </ul>
                <Button buttonSize='btn--wide' buttonColor='primary'>
                Continue
                </Button>
              </div>
            </Link>
            <Link to='/portfolio' className='pricing__container-card' data-aos='zoom-out-down'>
              <div className='pricing__container-cardInfo' >
                <div className='icon'>
                  <BsXDiamondFill />
                </div>
                
                <ul className='pricing__container-features'>
                  <li>A 4 ways concrete duct for SPDC shell.</li>
                  <li>Built all CANtv stations in Port Harcourt, Yenagoa, Calabar, Eket and Umuahia</li>
                  <li>Built lovefm stations in Port Harcourt and Umuahia.</li>
                </ul>
                <Button buttonSize='btn--wide' buttonColor='blue'>
                Continue
                </Button>
              </div>
            </Link>
            <Link to='/portfolio' className='pricing__container-card' data-aos='flip-left'>
              <div className='pricing__container-cardInfo'  >
                <div className='icon'>
                  <GiCrystalize />
                </div>
                
                <ul className='pricing__container-features'>
                  <li> CATV mini head-end at the UPTH resident doctors’ quarters</li>
                  <li>CATV mini head-end and installed an intercom PABX.</li>
                  <li>Cabling work (SOC) for intercom equipment (MITEL 200 lite PABX) </li>
                </ul>
                <Button buttonSize='btn--wide' buttonColor='primary'>
                  Continue
                </Button>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </IconContext.Provider>
  );
}
export default Pricing;
